	import topBar from '../../components/TopBar.vue'
	export default{
		data(){
			return{
				dataInfo:{},
				pageNo: 1,
				pageSize: 10,
				sortBy:'',
				loadingText: '',
				listDatas: [],
				loading:false
			}
		},
		components: {
			topBar
		},
		onLoad() {
			this.getDataInfo()
		},
        methods: {
            getDataInfo(){
				console.log(700,this.pageNo)
				this.request({
					url: `transaction/my/tenders/q`,
					method: 'POST',
					params: {
						pageNo:this.pageNo,
						pageSize:this.pageSize,
						sortBy:this.sortBy
					}
				}).then(res => {
					let listDatas = this.listDatas
					this.listDatas = listDatas.concat(res.data.result.content)
					this.dataInfo = res.data.result
					this.loadingText= ''
				    this.loading = false
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '列表数据请求失败，请稍后重试！',
					});
				})
			},
			dataAdd(){
				if (this.pageNo == this.dataInfo.totalPage){
					this.loadingText= '没有更多了'
					this.loading = true
				}else{
					this.pageNo++
					this.getDataInfo()
				}
			},
			loadMore(e){
				this.loadingText= '数据加载中...'
				this.loading = true
				this.dataAdd()
			}
        }
	}
